<template>
  <div d="gitHubWorks">
    <div class="row align-items-center">
      <h5 class="col">GitHub <a class="btn btn-info float-right"
                                href="https://github.com/curatorjin" target="_blank">更多</a></h5>
    </div>
    <div class="d-flex justify-content-left flex-wrap mb-3">
      <div class="p-2 card" v-for="item in works" :key="item.id">
        <h5 style="height: 30px;" class="font-weight-bold" v-html="item.name"></h5>
        <p style="height: 160px;" v-html="item.desc"></p>
        <a v-bind:href="item.link">去GitHub查看</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GitHub',
  data () {
    return {
      works: ''
    }
  },
  mounted: function () {
    this.getBiliWork()
  },
  methods: {
    getBiliWork: function () {
      this.$http.get('static/github_works.json').then(function (response) {
        this.works = response.data
      })
    }
  }
}
</script>

<style scoped>
  .card {
    width: 345px;
    height: 180px;
    margin: 15px;
    text-align: left;
  }
</style>



