<template>
  <div id="works" class="board img-thumbnail">
    <div class="row align-items-center">
      <div class="col-10">
        <h3 class="text-center" style="padding-top: 15px">一些作品……</h3>
      </div>
      <div class="col-2">
        <div class="btn-group" style="margin-top: 20px;margin-bottom: 20px;">
          <button id="gitBtn" type="button" class="btn btn-info active" @click="githubWorks">
            GitHub
          </button>
          <button id="biliBtn" type="button" class="btn btn-info" @click="biliWorks">Bilibili
          </button>
        </div>
      </div>
    </div>
    <hr/>
    <component :is="showing"></component>
    <div class="row">

    </div>
  </div>
</template>

<script>
import GitHub from './GitHub'
import Bilibili from './Bilibili'

export default {
  name: 'Works',
  data() {
    return {
      showing: 'GitHub'
    }
  },
  methods: {
    githubWorks: function () {
      $('#biliBtn').removeClass('active')
      $('#gitBtn').addClass('active')
      this.showing = 'GitHub'
    },
    biliWorks: function () {
      $('#gitBtn').removeClass('active')
      $("#biliBtn").addClass('active')
      this.showing = 'Bilibili'
    }
  },
  components: {
    GitHub,
    Bilibili
  }
}
</script>

<style scoped>
  #works {
    margin-top: 20px;
  }
</style>



