<template>
  <div id="biliWorks" class="align-items-center">
    <div class="row align-items-center">
      <h5 class="col">bilibili的作品 <a class="btn btn-info float-right"
                                     href="https://space.bilibili.com/2137919"
                                     target="_blank">更多</a></h5>
    </div>
    <div class="d-flex justify-content-center flex-wrap mb-3">
      <div class="p-2 video card" v-for="item in works" :key="item.id" @click="showVideo(item.aid)"
           data-toggle="modal" data-target="#videoModal">
        <div class="imgContent">
          <img class=" img-thumbnail" v-bind:src="item.pic"/>
        </div>
        <div v-bind:title="item.title" class="video-title">{{item.title}}</div>
      </div>
    </div>

    <div class="modal fade" id="videoModal" aria-hidden="true" data-backdrop="static">
      <div class="modal-dialog modal-lg" style="width: 1000px;height: 1000px;">
        <div class="modal-content">
          <div class="modal-body">
            <iframe style="width: 640px;height: 480px;" v-bind:src="playingVideo"
                    scrolling="no" frameborder="no"
                    allowfullscreen="true"></iframe>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"
                    v-on:click="closeModal">返回
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Bilibili',
  data () {
    return {
      works: '',
      playingVideo: ''
    }
  },
  mounted: function () {
    this.getBiliWork()
  },
  methods: {
    getBiliWork: function () {
      this.$http.get('static/bilibili_works.json').then(function (response) {
        var videoList = response.data.data.vlist
        this.works = videoList
      })
    },
    showVideo: function (videoAddr) {
      this.playingVideo = '//player.bilibili.com/player.html?aid=' + videoAddr + '&cid=68254646&page=1'
    },
    closeModal: function () {
      this.playingVideo = ''
    }
  }
}
</script>

<style scoped>
  .video {
    width: 300px;
    height: 230px;
    margin: 15px;
  }

  .video-title {
    width: 200px;
    height: 50px;
    overflow: hidden;
    text-align: left;
  }
</style>



